import React, { useRef, useState } from 'react';
import Typography from '@mui/material/Typography';
import { Box } from '@mui/system';

import Meta from 'components/Meta';
import { FullSizeFlexBox } from 'components/styled';
import LoadingButton from '@mui/lab/LoadingButton';
import { useNavigate, useSearchParams } from 'react-router-dom';
import Header from 'components/Layout/Header';
import { useTranslation } from 'react-i18next';
import { LayoutSafeArea } from 'components/Layout/LayoutSafeArea';
import { useAppDispatch, useAppSelector } from 'app/hooks';
import { selectClientId, selectDeviceInfo } from 'app/app.slice';
import CustomInput from 'components/CustomInput';
import { CustomInputHandle } from 'components/CustomInput/types';
import { authSagaAction } from 'features/auth/states/auth/auth.saga';
import { selectAccountInfo } from 'features/auth/states/account/account.slice';
import { CallbackForgotPassProfileResponse } from 'features/auth/states/auth/auth.types';
import alertDialogStore from 'components/AlertDialog/alertDialogStore';
import { selectLoading } from 'features/auth/states/auth/auth.slice';
import { formatSecondToMin } from 'utils/format-utils';
import callCustomerCare from 'utils/callCustomerCare';

function VerifyBank() {
  const dispatch = useAppDispatch()
  const deviceInfo = useAppSelector(selectDeviceInfo)
  const accountInfo = useAppSelector(selectAccountInfo)
  const clientId = useAppSelector(selectClientId)
  const loading = useAppSelector(selectLoading)

  const [searchParams] = useSearchParams()
  const type = searchParams.get('type')
  const { t } = useTranslation()
  const navigate = useNavigate()
  const inputRef = useRef<CustomInputHandle>(null)
  const buttonRef = useRef(null);
  const [value, setValue] = useState<string>('');

  const onChangeText = (event: React.ChangeEvent<HTMLInputElement>) => {
    inputRef.current?.hideError()
    setValue(event.target.value)
    return event.target.value
  }

  const onSubmit = () => {
    if (inputRef.current?.value) {
      if (type === 'cardNumber') { // Trường hợp tài khoản có thẻ ngân hàng
        dispatch(authSagaAction.forgotPassProfile({
          phone: accountInfo?.phone,
          clientId,
          confirmInfo: {
            cardNumber: inputRef.current?.value
          }
        }, (status: boolean, response: CallbackForgotPassProfileResponse) => {
          if (status) {
            if (response.code === 200306) {
              navigate(`/reset-pin?confirmCode=${response.data.confirmCode}`)
            } else if (response.code === 200315) {
              const remainingTime = formatSecondToMin(response.data.remainingTime || 0)
              alertDialogStore.alertContent = {
                title: 'Tạm khoá xác minh tài khoản',
                subtitle: [`Bạn đã nhập sai số thẻ/STK ${response.data.times || 'nhiều'} lần.\nThử lại sau`, remainingTime],
                firstButtonTitle: 'Đã hiểu',
              }
            } else if (response.code === 200325) {
              alertDialogStore.alertContent = {
                title: 'Tài khoản của bạn đã bị khoá',
                subtitle: 'Liên hện CSKH ngay để được hỗ trợ',
                firstButtonTitle: 'Liên hệ CSKH',
                onClickFirstButton: () => callCustomerCare(),
              }
            } else {
              inputRef.current?.setErrorMessage(response.message)
              inputRef.current?.showError()
            }
          } else {
            alertDialogStore.alertContent = {
              title: 'Lỗi kết nối',
              subtitle: response.message,
              firstButtonTitle: 'Đã hiểu'
            }
          }
        }))
      } else if (type === 'accountNumber') {  // Trường hợp tài khoản có stk ngân hàng
        dispatch(authSagaAction.forgotPassProfile({
          phone: accountInfo?.phone,
          clientId,
          confirmInfo: {
            accountNumber: inputRef.current?.value
          }
        }, (status: boolean, response: CallbackForgotPassProfileResponse) => {
          if (status) {
            if (response.code === 200306) {
              navigate(`/reset-pin?confirmCode=${response.data.confirmCode}`)
            } else if (response.code === 200315) {
              const remainingTime = formatSecondToMin(response.data.remainingTime || 0)
              alertDialogStore.alertContent = {
                title: 'Tạm khoá xác minh tài khoản',
                subtitle: [`Bạn đã nhập sai số thẻ/STK ${response.data.times || 'nhiều'} lần.\nThử lại sau`, remainingTime],
                firstButtonTitle: 'Đã hiểu',
              }
            } else if (response.code === 200325) {
              alertDialogStore.alertContent = {
                title: 'Tài khoản của bạn đã bị khoá',
                subtitle: 'Liên hện CSKH ngay để được hỗ trợ',
                firstButtonTitle: 'Liên hệ CSKH',
                onClickFirstButton: () => callCustomerCare(),
              }
            } else {
              inputRef.current?.setErrorMessage(response.message)
              inputRef.current?.showError()
            }
          } else {
            alertDialogStore.alertContent = {
              title: 'Lỗi kết nối',
              subtitle: response.message,
              firstButtonTitle: 'Đã hiểu'
            }
          }
        }))
      }
    }
  };

  return (
    <LayoutSafeArea>
      <Meta title="CreatePin" />
      <FullSizeFlexBox
        padding={2.5}
        paddingTop={deviceInfo?.platform?.toUpperCase() === 'IOS' || deviceInfo?.platform?.toUpperCase() === 'ANDROID' ? 0 : 2.5}
        paddingBottom={deviceInfo?.platform?.toUpperCase() === 'IOS' || deviceInfo?.platform?.toUpperCase() === 'ANDROID' ? 0 : 2.5}
        display="flex"
        flexDirection="column"
        justifyContent="space-between"
      >
        <Box>
          <Header isIconBack onClick={() => navigate(-1)} />
          <Box sx={{ paddingTop: 2.5 }} />
          <Typography variant='h2'>
            {t('auth.bankConfirm')}
          </Typography>
          <Box sx={{ paddingTop: 1 }} />
          <CustomInput
            ref={inputRef}
            placeholder='02332344'
            errorMessage='Số số thẻ/STK không đúng. Bạn kiểm tra và nhập lại.'
            onChange={onChangeText}
          />
        </Box>
        <LoadingButton
          ref={buttonRef}
          disabled={!value}
          loading={loading}
          loadingIndicator="Xin đợi..."
          variant="contained"
          disableElevation
          onClick={onSubmit}
        >
          {t('auth.verify')}
        </LoadingButton>
      </FullSizeFlexBox>
    </LayoutSafeArea>
  );
}

export default VerifyBank;
